.tool-link-div {
    text-align: center;
    margin: 3%;
    width: 30%;
    height: 60%;
    background-color: #0d1019;
    border-radius: 1vh;
    cursor: pointer;

}

.tool-name {
    padding: 1vh;
    font-size: 2.25vh;
    color: #0984e3;
}

.tool-text {
    display: none;
    color: white;
    font-size: 1.5vh;
    padding: 1vh;
}

.tool-link-div:hover .tool-name {
    display: none;
}

.tool-link-div:hover .tool-text {
    display: block;
}

.tool-link-div:hover {
    transform: scale(1.1);
    transition: ease-in-out;
}