body {
    margin: 0;
      font-family: 'Epilogue', sans-serif;
    font-weight: 500;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  .endzone-nav {
    height: 10vh;
    width: 100vw;
    display: flex;
    background-color: #0a0d14;
    
  }
  
  .endzone-nav-custom-border {
    width: 100%;
    border-bottom: .5vh solid #353b48;
    transform: scale(1);
      animation: pulse 3s infinite;
  }
  
  @keyframes pulse {
      0% {
          box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.7);
  
      }
  
      70% {
          box-shadow: 0 0 0 .5vh rgba(0, 0, 0, 0);
      }
  
      100% {
          box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
      border-bottom: .5vh solid #0984e3;
      }
  }
  
  .endzone-nav a{
    text-decoration: none;
    color: white;
    font-size: 2.25vh;
    padding-bottom: .5vh;
    border-bottom: solid .1vh #0984e3;
  }
  
  .endzone-nav a:hover {
    color: #0984e3;
    transition: all .3s ease-in-out;
    border-bottom: solid .1vh white;
  }
  
  .user-div button {
    position: relative;
    font-family: 'Epilogue', sans-serif;
    font-weight: 500;
    background-color: #0a0d14;
    color: white;
    border: 0;
    border-radius: .75vh;
    font-size: 2vh;
    padding: 1vh 1vw;
    cursor: pointer;
    height: 50%;
    top: 25%;
  }
  .endzone-nav button:hover {
    transform: scale(1.01); 
    animation: content_pulse 2s infinite;
  }
  
  .dropdown-content {
    position: relative;
    top: 50%;
    display: none;
    background-color: #252830;
    min-width: 10vw;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }

/* Links inside the dropdown */
.dropdown-content a {
    float: none;
    color: #0984e3;
    padding: 1vh 1vw;
    text-decoration: none;
    display: block;
    text-align: left;
  }

    /* Add a grey background color to dropdown links on hover */
    .dropdown-content a:hover {
            background-color: #ddd;
    }

  /* Show the dropdown menu on hover */
  .user-div:hover .dropdown-content {
    display: block;
  }

  @keyframes content_pulse {
      0% {
          box-shadow: 0 0 0 0 rgba(9, 132, 227, .75);
          border: .1vh white;
      }
  
      33% {
          box-shadow: 0 0 0 .5vh rgba(9, 132, 227, .5);
      }
  
      66% {
          box-shadow: 0 0 0 1vh rgba(9, 132, 227, .25);
      }
  
      100% {
          box-shadow: 0 0 0 0 rgba(9, 132, 227, 0);
      }
  }
  
  .endzone-nav button:hover {
    background-color: #0984e3; 
    transition: all .3s ease-in-out;
    transform: scale(1.1);
  }
  
  .endzone-nav .user-div {
    margin-left: auto;
    margin-bottom: auto;
    height: 100%;
    width: 33.5%;
    background-color: #252830;
    border-left: .5vh solid #353b48;
    text-align: center;
  }
  
  .logo-container {
    width: 20vw;
    height: 10vh;

  }
  
  .logo-container .logo {
    width: 100%;
    height: 100%;
    padding: 0,0,0;
    cursor: pointer;
  }