.endzone-search-bar {
    text-align: center;
    margin: 2vh;
}

.search-entry {
    width: 100%;
}

.search-results {
    position: relative;
    top: 50%;
    display: none;
    background-color: #252830;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    width: 100%;
  }

/* Links inside the dropdown */
.search-results a {
    float: none;
    color: #0984e3;
    padding: 1vh;
    text-decoration: none;
    display: block;
    text-align: left;
    width: 100%;
  }

/* Add a grey background color to dropdown links on hover */
.search-results a:hover {
        background-color: #ddd;
}
