@import url('https://fonts.googleapis.com/css2?family=Epilogue:wght@300&500&display=swap');

body {
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: 'Epilogue', sans-serif;
}

.hubPage {
    position: relative;
    width: 100%;
    height: 100%;
}

.hub-body {
    display: inline-flex;
    justify-content: space-evenly;
    align-items: stretch;
}

.hub-menu {
    display: inline-flex;
    justify-content: space-evenly;
    position: relative;
    float: right;
    width: 80vw;
    height: auto;
}

.hub-left, .hub-right {
    display: inline-block;
    justify-content: center;
    width: 33vw;
    height: 90vh;

}
