.tool-folder {
    background-color: #252830;
    width: 75%;
    height: 25%;
    margin-top: 7.25%;
    margin-bottom: 7.25%;
    margin-left: 12.5%;
    margin-right: 12.5%;
    border-left: .25vh solid #353b48;
    border-right: .25vh solid #353b48;
    border-bottom: .25vh solid #353b48;
}

.tool-folder-heading {
    width: 100%;
    height: auto;
    background-color: #0d1019;
    text-align: center;
    border-top: .25vh solid #353b48;
    border-bottom: .25vh solid #0984e3;


}

.tool-folder-heading h3 {
    color: white;
    font-size: 2.5vh;
    margin: 1.5vh;
}

.tool-folder-body {
    opacity: .5;
    display: inline-flex;
    justify-content: space-evenly;
    width: 100%;
    height: 100%;

}

.tool-folder:hover .tool-folder-body{
    opacity: 1;
}



.tool-folder:hover {
    transform: scale(1.03); 
}